<template>
<div>
    <div class="drug"
    v-infinite-scroll="loadMore"
    infinite-scroll-disabled="loading"
    infinite-scroll-distance="1000"
    >
      <div
      v-for='item in list' :key='item.catalog.id'
      >
        <router-link :to='`/Businesslist/${item.catalog.id}`'>
          <div class="Img" id="container"><img v-lazy.container="item.catalog.img"></div>
          <div class="cataloglist">
            <h4>{{item.catalog.name}} {{item.catalog.dName}}</h4>
            <p class="count">{{item.catalog.count}}x{{item.catalog.dai}}袋/盒</p>
            <p class="type">{{item.catalog.type}}</p>
            <p class="drugCompany">{{item.catalog.yaofang}}{{item.catalog.dName}}大药房</p>
            <div class="money">
              <span class="price">￥{{item.catalog.price.toFixed(2)}}</span>
              <span class="shangjia"><i>{{item.catalog.shangjia}}</i> 家商家在售</span>
            </div>
          </div>
        </router-link>
      </div>
      <div class="nomore" v-if='loading'>显示更多</div>
      <div class="nomore" v-else>没有更多了......</div>
    </div>
</div>
</template>

<script>
import { getCatalog } from '@/service.js'
export default {
  name: 'catalog',
  data () {
    return {
      msg: 'catalog',
      list: [],
      loading: false
    }
  },
  methods: {
    loadMore () {
      this.loading = true
      setTimeout(() => {
        getCatalog().then(res => {
          if (res.data.code === '200') {
            this.list = res.data.data.data
            this.loading = false
          }
        })
      }, 2000)
    }
  }
}
</script>

<style lang='scss' scoped>
.drug {
  height: 100%;
  a{
    display: flex;
    padding-top: 10px;
    padding-bottom: 5px;
    border-bottom: 1px solid #dedede;
    .Img {
      width: 20%;
      padding-top: 20%;
      height: 0;
      position: relative;
      img {
        width: 90%;
        height: 90%;
        position: absolute;
        left: 0;
        top:0;
        right: 0;
        bottom: 0;
        margin: auto;
        text-align: center;
      }
    }
    .cataloglist {
      width: 80%;
      line-height: 20px;
      h4 {
        font-weight: 600;
        color: #000;
      }
      .count, .type,{
        font-size: 12px;
        color: #999;
      }
      .drugCompany {
        font-size: 14px;
        color: #999;
      }
      .money {
        display: flex;
        justify-content: space-between;
        .price {
          color: red;
        }
        .shangjia {
          padding-right: 10px;
          color:#999;
          i {
            color: #00aa22;
            font-style: normal;
          }
        }
      }
    }
  }
  .nomore {
    width: 100%;
    margin-bottom: 60px;
    text-align: center;
  }
}
</style>



